  /*.weui-uploader__input-box:after {*/
  /*background-color: unset !important;*/
  /*}*/
}
.vux-upload .vux-flexbox-item .vux-upload-bg .loading:before,
.vux-upload .vux-flexbox-item .vux-upload-bg .loading:after {
  width: 0;
  height: 0;
}
.vux-upload .vux-flexbox-item .vux-upload-bg .weui-loading {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -15px;
  margin-left: -15px;
}
.vux-upload .vux-flexbox-item .vux-upload-bg .vux-x-icon {
  fill: #F70968;
  position: absolute;
  top: 0;
  left: 100%;
  width: 20px;
  height: 20px;
  -webkit-transform: translate(-50%, -50%);
