.is-inrange .pika-button{background:#d5e9f7
}
.is-startrange .pika-button{color:#fff;background:#6cb31d;-webkit-box-shadow:none;box-shadow:none;border-radius:3px
}
.is-endrange .pika-button{color:#fff;background:#3af;-webkit-box-shadow:none;box-shadow:none;border-radius:3px
}
.is-disabled .pika-button,.is-outside-current-month .pika-button{pointer-events:none;cursor:default;color:#999;opacity:.3
}
.pika-button:hover{color:#fff;background:#ff8000;-webkit-box-shadow:none;box-shadow:none;border-radius:3px
}
.pika-table abbr{border-bottom:none;cursor:help
}
