.vux-drawer > .drawer-right {
  right: 0;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.vux-drawer > .vux-drawer-active {
  pointer-events: inherit;
  visibility: visible;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
